(function() {
    'use strict';
    angular
        .module('app.auth')
        .factory('authenticationService', ['$resource', '$rootScope', '$q', authenticationService]);

    function authenticationService($resource, $rootScope, $q) {
        var users = $resource('../user/', { id: '@id' }, {
            isUserAuthentic: {
                method: 'GET',
                url: window.appBaseUrl + '/user/isUserAuthentic/',
                params: {
                    username: '@username',
                    password: '@password'
                }
            },
          getInstalledServices: {
            method: 'GET',
            url: window.appBaseUrl + '/microservice/list'
          }

        });
        return {
            get_api: users,
            'isUserAuthentic': function(params) {
                var defered = $q.defer();
                users.isUserAuthentic(params, function(response) { defered.resolve(response); },
                    function(error) { defered.reject(error); });
                return defered.promise;
            },
            'getInstalledServices': function() {
                var defered = $q.defer();
                users.getInstalledServices(function(response) { defered.resolve(response); },
                    function(error) { defered.reject(error); });
                return defered.promise;
            }
        }
    }

})();

