/*
Modifications to integrate UMM:
1. Changed all the 'data' variables because the data structure of the response
is different in UMM.
2. All the individual changes have been marked with *UMM comment.
*/
(function(){
  'use strict';
  angular
    .module('app.auth')
    .factory('Session',Session);


  function Session($rootScope,$http){
    var username= undefined;
    var userEmail= undefined;
    var userId= undefined;
    var teams = undefined;
    var userToken= undefined;
    var userPermissions= undefined;
    var userRole= [];
    var userDetails = undefined;

    var APRole = function(micros){
      var roles=[];
      var value=[];
      for(var i=0; i<micros.length;i++){
        roles = micros[i].roles;
        if(micros[i].name== "efadminpanel"){
          for(var j=0; j<roles.length;j++){
            value.push(roles[j].name)
          }
          return value;
        }
      }
    }
    return{
      create:function(data,rememberMe){
        // *UMM
        // added userDetails in all variable assignments except for the token
        // values.
        username= data.userDetails.username;
        userEmail= data.userDetails.email;
        userId= data.userDetails.id;
        userToken= data.token;
        $rootScope.token= data.token;
        $rootScope._user= data.userDetails;
        $rootScope.remember_me = rememberMe;
        $rootScope.teams=data.teams;
        $rootScope.userRole = APRole(data.userDetails.microservices);
        $rootScope.userDetails = data.userDetails.microservices;
        if(rememberMe){
          window.localStorage.setItem("username", data.userDetails.username);// = JSON.stringify(data);
          window.localStorage.setItem("token", data.token_type + " " + data.token);// = JSON.stringify(data);
          window.localStorage.setItem("userEmail", data.userDetails.email);// = JSON.stringify(data);
          window.localStorage.setItem("userRole", APRole(data.userDetails.microservices));// = JSON.stringify(data);
          window.localStorage.setItem("userDetails", data.userDetails.microservices);// = JSON.stringify(data);
          window.localStorage.setItem("type", data.userDetails.type);// = JSON.stringify(data);
          window.localStorage.setItem("profileExists", data.userDetails.profileExists);// = JSON.stringify(data);
        }
        else{
          window.sessionStorage.setItem("token", data.token_type + " " + data.token);
          window.sessionStorage.setItem("username", data.userDetails.username);
          window.sessionStorage.setItem("email", data.userDetails.email);
          window.sessionStorage.setItem("userRole", APRole(data.userDetails.microservices));
          window.sessionStorage.setItem("userDetails", data.userDetails.microservices);
          window.sessionStorage.setItem("type", data.userDetails.type);
          window.sessionStorage.setItem("profileExists", data.userDetails.profileExists);
        }
        // *UMM
        // Header name changed to Authorization and token format has been changed to "$token_type $token"
        $http.defaults.headers.common["Authorization"] = data.token_type + " " + data.token;
      },
      username:function(){
        return username ||  $rootScope._user? $rootScope._user.username:"" ;
      },
      userEmail:function(){
        return userEmail || $rootScope._user?$rootScope._user.email: "";
      },
      userId:function(){
        return userId || $rootScope._user?$rootScope._user.id :"";
      },
      userToken:function(){
        return userToken || $rootScope._user ? $rootScope.token :"";
      },
      userTeams:function(){
        return userToken || $rootScope.teams ? $rootScope.teams :"";
      },
      update:function(data){
        $rootScope._user= data.userDetails;
        username= data.userDetails.username;
        userEmail= data.userDetails.email;
        userId= data.userDetails.id;
        teams= data.teams;
        userPermissions = data.userPermissions;
        userRole= APRole(data.userDetails.microservices);
        userDetails = data.userDetails.microservices;
        window.teams=data.teams;
      },
      destroy : function () {
        window.localStorage.removeItem("username");
        window.localStorage.removeItem("token");
        window.localStorage.removeItem("userEmail");
        window.localStorage.removeItem("userRole");
        window.localStorage.removeItem("userDetails");
        window.sessionStorage.removeItem("username");
        window.sessionStorage.removeItem("token");
        window.sessionStorage.removeItem("email");
        window.sessionStorage.removeItem("userRole");
        window.sessionStorage.removeItem("userDetails");
        window.sessionStorage.removeItem("type");
        window.sessionStorage.removeItem("profileExists");
        username = null;
        userEmail = null;
        userId = null;
        userToken = null;
        teams=null;
        window.teams=null;
        window.userRole=null;
        window.userDetails=null;
        $rootScope._user = null;
        $rootScope.agentList=undefined;
        $http.defaults.headers.common["Authorization"]= undefined;
        // window.location.href = appBaseUrl+'/'
      },
      getUser:function(){
        return  {username:username,email:userEmail,id:userId,teams:teams}
      },
      setAuthToken:function(){
        $http.defaults.headers.common["Authorization"] = window.localStorage.getItem("token") || window.sessionStorage.getItem("token");
      }



    }
  }

})();

