(function () {
  'use strict';

  angular
    .module('app.users')
    .controller('RoleController', RoleController);

  /** @ngInject */
  function RoleController($rootScope, userService, utilCustom, contacts, Microservices, Permissions, $mdDialog, $filter, Role, Roles, msUtils) {
    var message;
    var vm = this;

    // Data
    vm.title = $filter('translate')('CONTACTS.editRole');
    vm.role = Role;
    vm.microservices = angular.copy(Microservices);
    vm.microservice = vm.microservices[1];
    vm.permissions = angular.copy(Permissions);
    vm.contacts = contacts;
    vm.roleUser = [];
    vm.roles = angular.copy(Roles);

    //vm.user = User;
    vm.newRole = false;
    vm.allFields = false;
    vm.translationData = {
      roles: $filter('translate')('CONTACTS.roles'),
      permissions: $filter('translate')('CONTACTS.permissions'),
      users: $filter('translate')('user.users'),
      secondarySupervisor: $filter('translate')('team.secondarySupervisor'),
      csq: $filter('translate')('csq.csqs'),
      agent: $filter('translate')('agent.agents'),
      applications: $filter('translate')('application.applications'),
      services: $filter('translate')('service.services')
    };

    if (!vm.role) {
      vm.role = {
        'name': null,
        'Description': null
      };

      vm.title = $filter('translate')('CONTACTS.newRole');
      vm.newRole = true;
    }

    // Methods
    vm.toggleInArrayPerm = toggleInArrayPerm;
    vm.toggleInArrayUser = toggleInArrayUser;
    vm.addNewRole = addNewRole;
    vm.saveRole = saveRole;
    vm.closeDialog = closeDialog;
    vm.exists = exists;
    vm.queryArraySearch = queryArraySearch;
    vm.queryUserSearch = queryUserSearch;
    vm.addPermission = addPermission;
    vm.removePermission = removePermission;
    vm.scrollToBottom = scrollToBottom;
    vm.userAssignment = userAssignment;
    vm.revokeUser = revokeUser;
    vm.filterUsers = filterUsers;
    vm.filterPermissions = filterPermissions;
    vm.removable = removable;
    vm.roleExists = roleExists;
    vm.uniqueRole = true;

    function roleExists(data) {
      var ind = _.findIndex(vm.roles, function (o) {
        return angular.lowercase(o.name) === angular.lowercase(data);
      });
      if (ind !== -1)
        vm.uniqueRole = false;
      else
        vm.uniqueRole = true;
    }

    function removable(name) {
      if (name === 'admin')
        return false;
      else
        return true;
    }


    if (vm.role.name !== null) {
      userService.roleUser({id: vm.role.id}).then(function (response) {
        //console.log(response);
        //utilCustom.hideToaster();
        mapUsers(response.users)
        //utilCustom.toaster($filter('translate')('CONTACTS.roleAssignmentSuccess'));
      }, function (error) {
        console.log(error);
      });
    }

    function userAssignment(role, chip) {
      var micros = extractRoleMicros(role);
      var params = {
        role: role.id,
        user: chip.id,
        microservices: micros,
        addRevoke: "add"
      };

      userService.userAssignment(params).then(function (response) {
        var ind = _.findIndex(vm.contacts, {id: response.user.id});

        if (response.user.profileExists)
          response.user.avatar = window.appBaseUrl + '/base/assets1/images/agents/' + angular.lowercase(response.user.username) + '.jpg?timestamp=' + new Date().getTime();
        else
          response.user.avatar = 'assets1/images/avatars/profile.jpg?timestamp=' + new Date().getTime();

        if (ind !== -1)
          vm.contacts[ind] = response.user;

        var ind2 = _.findIndex(vm.roleUser, {id: response.user.id});
        if (ind2 === -1)
          vm.roleUser.unshift(response.user);

        message = "contactUpdate";
        vm.roleForm.$setDirty();
      }, function (error) {
        utilCustom.toaster($filter('translate')('CONTACTS.roleAssignmentFailure'));
        console.log(error);
      });
    }

    function revokeUser(role, chip) {
      var micros = extractRoleMicros(role);
      var params = {
        role: role.id,
        user: chip.id,
        microservices: micros,
        addRevoke: "revoke"
      };

      userService.userAssignment(params).then(function (response) {
        var ind = _.findIndex(vm.contacts, {id: response.user.id});

        if (response.user.profileExists)
          response.user.avatar = window.appBaseUrl + '/base/assets1/images/agents/' + angular.lowercase(response.user.username) + '.jpg?timestamp=' + new Date().getTime();
        else
          response.user.avatar = 'assets1/images/avatars/profile.jpg?timestamp=' + new Date().getTime();

        if (ind !== -1)
          vm.contacts[ind] = response.user;

        var ind2 = _.findIndex(vm.roleUser, {id: response.user.id});
        if (ind2 !== -1)
          vm.roleUser.splice(ind2, 1);
        message = "contactUpdate";
        vm.roleForm.$setDirty();
      }, function (error) {
        utilCustom.toaster($filter('translate')('CONTACTS.roleRevokeFailure'));
        console.log(error);
      });

    }

    function extractRoleMicros(role) {
      var micros = [];
      for (var i = 0; i < role.permissions.length; i++) {
        micros.push({id: role.permissions[i].microservice});
      }
      micros = _.uniqBy(micros, 'id');
      return micros;
    }

    function mapUsers(users) {
      for (var i = 0; i < users.length; i++) {
        for (var j = 0; j < vm.contacts.length; j++) {
          if (users[i].id === vm.contacts[j].id) {
            vm.roleUser.push(vm.contacts[j]);
          }
        }
      }
    }

    function addPermission(ev, role, chip) {
      var requisites = [];
      var perms = "";
      if (chip.preReqs.length !== 0) {
        if(role.permissions.length!==0){
          angular.forEach(chip.preReqs, function (chipPerm) {
            var ind = _.findIndex(role.permissions, function (o) {
              return o.expression === chipPerm;
            });
            if(ind === -1){
              var full = chipPerm.split(":")[0]+ ":*";
              var ind2 = _.findIndex(role.permissions, function (o) {
                return o.expression === full;
              });
              if(ind2===-1)
                requisites.push(chipPerm);
            }
          });

          if(requisites.length!==0){
            perms = mapPerms(requisites);
          }else {
            return addPermissionAux(role, chip);
          }
        }else {
          perms = mapPerms(chip.preReqs);
        }

        var title = $filter('translate')('CONTACTS.preReqPermissions');
        var content = $filter('translate')('CONTACTS.assignPermsReqs') + "<br>" + perms + "<br>"
          + $filter('translate')('CONTACTS.assignPermsConfirm');
        var confirm = $mdDialog.confirm()
          .title(title)
          .htmlContent(content)
          .ariaLabel('assign perms pre-requisites')
          .targetEvent(ev)
          .ok($filter('translate')('generic.ok'))
          .cancel($filter('translate')('generic.cancel'))
          .multiple(true);

        $mdDialog.show(confirm)
          .then(function () {
            return addPermissionAux(role, chip);
          }, function () {
            return null;
          });
      } else {
        return addPermissionAux(role, chip);
      }

    }

    function mapPerms(permsToMap){
      var perms="";
      angular.forEach(permsToMap, function (preReq) {
        var lowerPre = angular.lowercase(preReq);
        var ind = _.findIndex(vm.permissions, function (o) {
          return angular.lowercase(o.expression) === lowerPre;
        });
        if(ind!==-1){
          perms += "&#9734; " +vm.permissions[ind].name +"<br>";
        }
      });
      return perms;
    }
    function addPermissionAux(role, chip) {
      var params = {
        id: role.id,
        permissions: [{id: chip.id}],
        addRevoke: "add"
      };
      userService.addRemPermissions(params).then(function (response) {
        vm.role = response.role;
        message = "update";
        vm.roleForm.$setDirty();
      }, function (error) {
        utilCustom.toaster($filter('translate')('CONTACTS.permAddFailure'));
        console.log(error);
      });
      return chip;
    }

    function removePermission(role, chip) {
      //var perms = extractPerms(chip);
      var params = {
        id: role.id,
        permissions: [{id: chip.id}],
        addRevoke: "revoke"
      };

      userService.addRemPermissions(params).then(function (response) {
        vm.role = response.role;
        message = "update";
        vm.roleForm.$setDirty();
      }, function (error) {
        utilCustom.toaster($filter('translate')('CONTACTS.permRemoveFailure'));
        console.log(error);
      });
      return chip;
    }

    function queryArraySearch(query, objectList) {
      var results = query ? objectList.filter(createFilterFor(query)) : [];

      for (var i = 0; i < vm.role.permissions.length; i++) {

        for (var i2 = 0; i2 < results.length; i2++) {

          if (results[i2].expression.toLowerCase() === vm.role.permissions[i].expression.toLowerCase()) {

            results.splice(i2, 1);

          }

        }

      }

      var ind = _.findIndex(objectList, function (o) {
        return o.expression === "default:*";
      });
      if (ind !== -1)
        results.splice(ind, 1);

      return results;

    }

    function queryUserSearch(query, objectList) {
      var results = query ? objectList.filter(createFilterForUser(query)) : [];

      for (var i = 0; i < vm.roleUser.length; i++) {

        for (var i2 = 0; i2 < results.length; i2++) {

          if (results[i2].id === vm.roleUser[i].id) {

            results.splice(i2, 1);

          }

        }

      }
      var ind = _.findIndex(objectList, function (o) {
        return o.username === "admin";
      });
      if (ind !== -1)
        results.splice(ind, 1);
      return results;

    }

    function createFilterForUser(query) {
      var lowercaseQuery = angular.lowercase(query);
      return function filterFn(item) {
        return angular.lowercase(item.fullName).indexOf(lowercaseQuery) >= 0;
      };
    }

    function createFilterFor(query) {
      var lowercaseQuery = angular.lowercase(query);
      return function filterFn(item) {
        return angular.lowercase(item.expression).indexOf(lowercaseQuery) >= 0;
      };
    }

    function scrollToBottom(id) {
      var objDiv = document.getElementById(id);
      objDiv.lastElementChild.lastElementChild.lastElementChild.focus()
      objDiv.scrollTop = objDiv.scrollHeight;
    }

    //////////

    /**
     * Add new contact
     */
    function addNewRole(role) {
      var userCreated = $rootScope._user;
      var params = {
        authority: role.name,
        description: role.description,
        createdBy: {id: userCreated.id}
      };
      // Dummy save action
      utilCustom.toasterLoading();
      userService.createRole(params).then(function (response) {

        vm.role = response.role;
        message = "create";
        utilCustom.toaster($filter('translate')('CONTACTS.successCreatedRole'));
        //$mdDialog.hide({role: vm.role, message: 'create'});
        closeDialog();
      }, function (error) {
        console.log(error);
      });

    }

    /**
     * Save contact
     */
    function saveRole(role) {
      var userUpdated = $rootScope._user;
      var params = {
        id: role.id,
        authority: role.name,
        description: role.description,
        createdBy: {id: userUpdated.id}
      };
      utilCustom.toasterLoading();
      userService.updateRole(params).then(function (response) {
        vm.role = response.role;
        message = "update";
        utilCustom.toaster($filter('translate')('CONTACTS.successUpdatedRole'));
        closeDialog();
        //$mdDialog.hide({role: vm.role, message: 'update'});
      }, function (error) {
        console.log(error);
      });


    }


    function filterPermissions(label) {
      if (!vm.labelSearchPermText || vm.labelSearchPermText === '') {
        return true;
      }

      return angular.lowercase(label.name).indexOf(angular.lowercase(vm.labelSearchPermText)) >= 0;
    }

    function filterUsers(label) {
      if (label.username === 'admin')
        return false;

      if (!vm.labelSearchUserText || vm.labelSearchUserText === '') {
        return true;
      }

      return angular.lowercase(label.fullName).indexOf(angular.lowercase(vm.labelSearchUserText)) >= 0;
    }

    function exists(item, list) {
      return _.find(list, {id: item.id});
    }

    function toggleInArrayPerm(ev, item, list) {
      var ind = _.findIndex(list, {id: item.id});
      if (ind !== -1) {
        removePermission(vm.role, item);
      } else {
        addPermission(ev, vm.role, item);
      }
    }

    function toggleInArrayUser(item, list) {
      var ind = _.findIndex(vm.roleUser, {id: item.id});
      if (ind !== -1) {
        revokeUser(list, item);
      } else {
        userAssignment(list, item);
      }
    }

    /**
     * Close dialog
     */
    function closeDialog() {
      $mdDialog.hide({role: vm.role, contacts: vm.contacts, message: message});
    }
  }
})();

(function ()
{
    'use strict';

    angular
        .module('app.users')
        .controller('ContactDialogController', ContactDialogController);

    /** @ngInject */
    function ContactDialogController( $rootScope, utilCustom, userService, $mdDialog, $filter, Roles, Contact, Contacts, User, msUtils, $document,$scope)
    {
        var vm = this;
        var message;
        // Data
        vm.title = $filter('translate')('CONTACTS.editUser');
        vm.contact = angular.copy(Contact);
        vm.contacts = Contacts;
        vm.roles = angular.copy(Roles);
        //vm.user = User;
        vm.newContact = false;
        vm.allFields = false;
        vm.showPassword= "password";
        vm.translationData = {
          roles: $filter('translate')('CONTACTS.roles'),
          secondarySupervisor: $filter('translate')('team.secondarySupervisor'),
          csq: $filter('translate')('csq.csqs'),
          agent: $filter('translate')('agent.agents'),
          applications:$filter('translate')('application.applications'),
          services:$filter('translate')('service.services')
        };

        if ( !vm.contact )
        {
            vm.contact = {
                'name'    : '',
                'lastName': '',
                'avatar'  : 'assets1/images/avatars/profile.jpg?timestamp=' + new Date().getTime(),
                'nickname': '',
                'company' : '',
                'jobTitle': '',
                'email'   : '',
                'phone'   : '',
                'address' : '',
                'birthday': null,
                'notes'   : '',
                'isActive' : false
            };

            vm.title = $filter('translate')('CONTACTS.newUser');
            vm.newContact = true;
            vm.contact.tags = [];
        }else
          vm.user = {file:vm.contact.avatar};

        // Methods
        vm.addNewContact = addNewContact;
        vm.saveContact = saveContact;
        vm.deleteContactConfirm = deleteContactConfirm;
        vm.closeDialog = closeDialog;
        vm.toggleInArray = msUtils.toggleInArray;
        vm.exists = exists;
        vm.queryArraySearch = queryArraySearch;
        vm.dialogShowDetail = dialogShowDetail;
        vm.roleAssignment = roleAssignment;
        vm.revokeRole = revokeRole;
        vm.togglePassword = togglePassword;
        vm.toggleInArrayRole = toggleInArrayRole;
        vm.filterRoles = filterRoles;
        vm.removable= removable;
        vm.usernameExists = usernameExists;
        vm.uniqueUsername = true;

        // $scope.$watch('vm.roles', function()
        // {
        //   vm.contactForm.$setDirty();
        // }, true);

        function usernameExists(data){
          var ind = _.findIndex(vm.contacts, function(o){
            return angular.lowercase(o.username)=== angular.lowercase(data);
          });
          if(ind!==-1)
            vm.uniqueUsername = false;
          else
            vm.uniqueUsername = true;
        }

        function removable(username){
            if(username === 'admin')
                return false;
            else
                return true;
        }

        function togglePassword(){
          if(vm.showPassword === "password")
            vm.showPassword = "text";
          else
            vm.showPassword = "password"
        }

        function roleAssignment(contact, chip){
          //var micros = extractMicros(chip);
          //vm.rolesAssigned.push(micros);
          var micros = extractMicros(chip);
          var params = {
            id: contact.id,
            microservices: micros,
            addRevoke:"add"
          };
          userService.assignRoles(params).then(function(response){
            //console.log(response);
            //utilCustom.hideToaster();
            vm.contact = response.user;
            message = "roleAssigned";
            vm.contactForm.$setDirty();
          },function(error){
            utilCustom.toaster($filter('translate')('CONTACTS.roleAssignmentFailure'));
            console.log(error);
          });
          return chip;
        }

        function revokeRole(contact, chip){
          var micros = extractMicros(chip);
          var params = {
            id: contact.id,
            microservices: micros,
            addRevoke:"revoke"
          };
          userService.assignRoles(params).then(function(response){
            vm.contact = response.user;
            message = "roleAssigned";
            vm.contactForm.$setDirty();
          },function(error){
            utilCustom.toaster($filter('translate')('CONTACTS.roleRevokeFailure'));
            console.log(error);
          });
          return chip;
        }

        function extractMicros(chip){
          var result = []
          for(var i=0; i<chip.permissions.length; i++){
            result.push({id: chip.permissions[i].microservice, roles: [{id: chip.id}]});
          }
          result = _.uniqBy(result, 'id');
          return result;
        }

        function queryArraySearch(query,objectList) {
          var results = query? objectList.filter(createFilterFor(query)): [];

          for (var i = 0; i < vm.contact.roles.length; i++) {

            for (var i2 = 0; i2 < results.length; i2++) {

              if (results[i2].name.toLowerCase() === vm.contact.roles[i].name.toLowerCase()) {

                results.splice(i2, 1);

              }

            }

          }
          return results;

        }

        function createFilterFor(query)
        {
          var lowercaseQuery = angular.lowercase(query);
          return function filterFn(item)
          {
            return angular.lowercase(item.name).indexOf(lowercaseQuery) >= 0;
          };
        }
        //////////

        /**
         * Add new contact
         */
        function addNewContact(user, file)
        {
          var userCreated = $rootScope._user;
          var params = {
            id:user.id,
            username: user.username,
            fullName: user.fullName,
            email: user.email,
            isActive: user.isActive,
            password: user.password,
            profileExists: file?true: false,
            createdBy:{id:userCreated.id}};
          // Dummy save action
          userService.create(params).then(function(response){

            vm.contact = response.user;

            if(file && file.name){
              var params2 = {file:file,agentId:angular.lowercase(user.username)};
              userService.updateProfilePic(params2).then(function(response){
                vm.contact.profileExists = true;
                utilCustom.toaster($filter('translate')('CONTACTS.createUserSuccess'));
                $mdDialog.hide({user: vm.contact, message: 'create'});
              },function(error){
                console.log(error);
              });
            }else{
              $mdDialog.hide({user: vm.contact, message: 'create'});
              utilCustom.toaster($filter('translate')('CONTACTS.createUserSuccess'));
            }

          }, function(error){
            utilCustom.toaster($filter('translate')('CONTACTS.createUserFailure'));
            console.log(error);
          });

        }

        /**
         * Save contact
         */
        function saveContact(user, file)
        {
            var userUpdated = $rootScope._user;
            var params = {
              id:user.id,
              fullName: user.fullName,
              email: user.email,
              isActive: user.isActive,
              profileExists: file?true: false,
              updatedby:{id:userUpdated.id}};
            // Dummy save action
            userService.update(params).then(function(response){
              vm.contact = response;
              if(file && file.name){
                var params2 = {file:file,agentId:angular.lowercase(Contact.username)};
                userService.updateProfilePic(params2).then(function(response){
                  vm.contact.profileExists = true;
                  utilCustom.toaster($filter('translate')('CONTACTS.updateUserSuccess'));
                  $mdDialog.hide({user: vm.contact, message: 'update'});
                },function(error){
                  console.log(error);
                });
              }else{
                utilCustom.toaster($filter('translate')('CONTACTS.updateUserSuccess'));
                $mdDialog.hide({user: vm.contact, message: 'update'});
              }

            }, function(error){
              utilCustom.toaster($filter('translate')('CONTACTS.updateUserFailure'));
              console.log(error);
            });


        }

        function dialogShowDetail(e, contact) {
          $mdDialog.show({
            controller: 'UserUploadPicController',
            controllerAs: 'vm',
            templateUrl: 'app/adminPanel/user/upload.html',
            parent: angular.element($document.body),
            targetEvent: e,
            clickOutsideToClose: true,
            skipHide: true,
            locals: {
              Contact : contact,
              User    : vm.user,
              Contacts: vm.contacts
            }
          }).then(function(userData) {
            if (userData) {
              //var ind = _.findIndex(vm.contacts, { username: userData.userID });
              //var indAge = _.findIndex(vm.agentList, { userID: userData.userID });
              //if (ind != -1) {
                if (userData.message === 'upload') {
                  vm.contact.avatar = window.appBaseUrl + 'base/assets1/images/agents/' + angular.lowercase(userData.username) + '.jpg?timestamp=' + new Date().getTime();
                } else if (userData.message === 'delete') {
                  vm.contact.avatar = 'assets1/images/avatars/profile.jpg?timestamp=' + new Date().getTime();
                }
              //}
            }
            $mdDialog.hide( {contact: vm.contact, message: userData.message});
          })
        }
        /**
         * Delete Contact Confirm Dialog
         */
        function deleteContactConfirm(ev)
        {
            var success = null;
            var confirm = $mdDialog.confirm()
                .title($filter('translate')('CONTACTS.ContactDeleteConfirm'))
                .ariaLabel('delete contact')
                .targetEvent(ev)
                .ok($filter('translate')('generic.ok'))
                .cancel($filter('translate')('generic.cancel'));

            $mdDialog.show(confirm).then(function ()
            {
              var params = {id:vm.contact.id};
              userService.delete(params).then(function(response){
                success = response;
                $mdDialog.hide({id: vm.contact.id, message: 'delete'});
              }, function(error){
                console.log(error);
              });
            });

        }

      function filterRoles(label) {
        if ( !vm.labelSearchRoleText || vm.labelSearchRoleText === '' )
        {
          return true;
        }

        return angular.lowercase(label.name).indexOf(angular.lowercase(vm.labelSearchRoleText)) >= 0;
      }

      function exists(item,list) {
        return _.find(list,{id:item.id});
      }

      function toggleInArrayRole(item, list){
        var ind = _.findIndex(list, { id: item.id });
        if(ind != -1){
          revokeRole(vm.contact,item);
        }else {
          roleAssignment(vm.contact,item);
        }
      }

        /**
         * Close dialog
         */
        function closeDialog()
        {
            $mdDialog.hide({user: vm.contact, message: message});
        }

    }
})();

